import auth from '@/components/auth.vue';
import api from '@/common/vmeitime-http/'
export default {
	data() {
		return {
			orderId: 0,
			type: 0,
			valueId: 0,
			orderGoods: {},
			content: '',
			stars: [0, 1, 2, 3, 4],
			star: 5,
			starText: '十分满意',
			hasPicture: false,
			picUrls: [],
			files: []
		}
	},
	onLoad(options){
		this.orderId = options.orderId,
		this.type = options.type,
        this.valueId = options.valueId
		console.log(options)
		this.getOrderGoods();
	},
	methods: {
		chooseImage: function(e) {
		    if (this.files.length >= 5) {
		      util.showErrorToast('只能上传五张图片')
		      return false;
		    }
		    var that = this;
		    uni.chooseImage({
		      count: 1,
		      sizeType: ['original', 'compressed'],
		      sourceType: ['album', 'camera'],
		      success: function(res) {
		          that.files = that.files.concat(res.tempFilePaths)
		          that.upload(res);
		      }
		    })
		  },
		  upload: function(res) {
		    var that = this;
		    const uploadTask = uni.uploadFile({
		      url: 'https://mall1.zhihuiyunqi.com/wx/storage/upload',
		      filePath: res.tempFilePaths[0],
		      name: 'file',
		      success: function(res) {
		        var _res = JSON.parse(res.data);
		        if (_res.errno === 0) {
		          var url = _res.data.url
		          that.picUrls.push(url)
		          that.hasPicture = true,
		          that.picUrls = that.picUrls
		        }
		      },
		      fail: function(e) {
		        uni.showModal({
		          title: '错误',
		          content: '上传失败',
		          showCancel: false
		        })
		      },
		    })
		    uploadTask.onProgressUpdate((res) => {
		      console.log('上传进度', res.progress)
		      console.log('已经上传的数据长度', res.totalBytesSent)
		      console.log('预期需要上传的数据总长度', res.totalBytesExpectedToSend)
		    })
		  },
		  previewImage: function(e) {
		    uni.previewImage({
		      current: e.currentTarget.id, // 当前显示图片的http链接
		      urls: this.files // 需要预览的图片http链接列表
		    })
		  },
		  selectRater: function(e) {
		    var star = e.currentTarget.dataset.star + 1;
		    var starText;
		    if (star == 1) {
		      starText = '很差';
		    } else if (star == 2) {
		      starText = '不太满意';
		    } else if (star == 3) {
		      starText = '满意';
		    } else if (star == 4) {
		      starText = '比较满意';
		    } else {
		      starText = '十分满意'
		    }
		  this.star = star,
		  this.starText = starText
		  },
		  getOrderGoods: function() {
		    let that = this;
		    api.httpget('/order/goods?orderId='+this.orderId+'&goodsId='+this.valueId).then(function(res) {
		      if (res.data.errno === 0) {
		          that.orderGoods = res.data.data
		      }
		    });
		  },
		  onClose: function() {
		    uni.navigateBack();
		  },
		  onPost: function() {
		    let that = this;
		    if (!this.content) {
			  uni.showToast({title: '请填写评论', icon: 'none'})
		      return false;
		    }
		    api.httppost('/order/comment', {
		      orderGoodsId: that.orderGoods.id,
		      content: that.content,
		      star: that.star,
		      hasPicture: that.hasPicture,
		      picUrls: that.picUrls
		    }).then(function(res) {
		      if (res.data.errno === 0) {
		        uni.showToast({
		          title: '评论成功',
		          complete: function() {
		            uni.switchTab({
		              url: '/pages/user/user'
		            })
		          }
		        })
		      } else {
				  
				  
			  }
		    });
		  },
		  bindInputValue(event) {
		    let value = event.detail.value;
		    //判断是否超过140个字符
		    if (value && value.length > 140) {
		      return false;
		    }
		    this.content = event.detail.value
		  }
	}
}
