	import api from '@/common/vmeitime-http/'
	import {
	    mapState, mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				haslogin: true,
				agentId: 7813,
				showActionSheet: false,
				maskClosable: true,
				itemList: [],
				color: "#9a9a9a",
				size: 26,
				hidemask: false,
				isCancel: true
			};
		},
		created() {

			if (uni.getStorageSync('haslogin') == '') {
				this.haslogin = false
			} 


			 this.getUserInfo();

		},
		methods: {
			...mapMutations(['login']),
			itemClick: function(e) {
				let index = e.index;
				this.closeActionSheet();
			},
			openauth(){

				this.haslogin = this.hidemask = false;




			},
			closeActionSheet: function() {
				this.showActionSheet = false
			},
			getUserInfo: function() {
				uni.showLoading({title: '正在加载'})
				let that = this;












































				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						let code = loginRes.code;
						// 获取用户信息
						uni.getUserInfo({
							provider: 'weixin',
							lang:'zh_CN',
							success: function(infoRes) {
								let userInfo = infoRes.userInfo;
								var options = {
									code: code,
									userInfo: userInfo
								}
								uni.setStorageSync('code', options.code);
								// console.log(options)
								that.login(userInfo)
								// 调用登录接口
								that.gologin(options)
							},
							fail: function() {
								uni.hideLoading()
								that.hidemask = true
								uni.navigateTo({
									url: '/pages/public/login'
								})
								console.log('获取信息失败')
							}
						})
					},
					fail: function(loginRes) {
						if (uni.getStorageSync('haslogin') == '') {
							that.haslogin = false
						}
					}
				})

			},
			gologin(options, data) {
				let that = this;
				let loginurl = '/auth/login_by_weixin';
				api.httppost(loginurl, options).then((res) => {
					if (res.data.errno == 0) {
						uni.setStorageSync('token', res.data.data.token);
						that.haslogin = true
						that.hidemask = true
						uni.setStorageSync('haslogin', true);
						that.$parent.getdata();
					} else {
						uni.navigateTo({
							url: '/pages/public/login'
						})
					}
					uni.hideLoading()
				}).catch((err) => {
					console.log(err)
					// uni.showToast({title: err.data.errmsg, icon: 'none'})
					uni.hideLoading()
				})
			}
		}
	}
